@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.App>img:first-child{
  position: absolute;
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  z-index: -100;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

footer{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 8rem;
}
footer ul.horizontal>li{
  padding: 0.5rem 1rem 0.5rem 0rem;
}
footer p{
  color: var(--white-50);
}
footer a{
  color: var(--white-50);
  border-bottom: 2px solid var(--white-50);
  transition: 0.5s;
}
footer a:hover{
  color: white;
  border-bottom: 2px solid white;
}

.headersvg{
  position: relative;
  display: flex;
  justify-content: center;
}
.headersvg>span{
  position: absolute;
  display: flex;
  justify-content: center;
  color: white;
  font-weight: 400;
  background-color: var(--main-color);
  opacity: 0;
}
.headersvg>span:hover{
  color: white;
}
.headersvg>span:after{
  position: absolute;
  content: '';
  background-color: transparent;
  width: 0; 
  height: 0;
}
.headersvg:hover>span{
  opacity: 1;
}
.headersvg svg{
  color: var(--white-50);
  background-color: transparent;
  transition: 0.5s;
}
.headersvg.active svg{
  color: white;
  background-color: transparent;
}
.headersvg:hover svg{
  color: white;
  background-color: transparent;
}
.headersvg>svg:last-child{
  position: absolute;
  background-color: transparent;
  width: 0; 
  height: 0; 
  transition: 0.25s;
  bottom: -2.5rem;
}
.headersvg.active>svg:last-child{
  position: absolute;
  background-color: transparent;
  width: 0; 
  height: 0; 
  bottom: -2.5rem;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.SectionAnimation{
  position: relative;
  display: flex;
  justify-content: center;
  background-color: transparent;
}
.SectionAnimation>section{
  transition: 0.5s;
}
.SectionAnimation>section>*{
  opacity: 1;
}
.SectionAnimation>section.fade-enter-active {
  opacity: 0;
  transition: 0s;
}
.SectionAnimation>section.fade-enter-done {
  opacity: 1;
  transition: 0s;
}
.SectionAnimation>section.fade-enter-active>* {
  opacity: 0;
  transition: 0s;
}
.SectionAnimation>section.fade-enter-done>* {
  opacity: 1;
  transition: all 0.5s ease-in;
}
.SectionAnimation>section:nth-child(2){
  opacity: 1;
  position: absolute;
  overflow: hidden;
  transition: all 0.5s ease-out;
}
.SectionAnimation>section:nth-child(2)>*{
  opacity: 0;
  transition: all 0.5s ease-out;
}

.App-header {
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

