section{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.row2_1{
    display: flex;
    align-items: center;
    align-content: flex-start;
    width: 100%;
    overflow: hidden;
}
.row2_1>article:first-child{
    text-align: start;
}
.row2_1>article:last-child{
    width: 100%;
}
.row2_1>article>*{
    margin: 0;
}
.row2_1>article>a{
    overflow: hidden;
    width: 100%;
}
.row2_1>article img{
    width: 100%;
    object-fit: cover;
    border-radius: 0;
}
.next{
    position: relative;
    display: flex;
    align-items: center;
}
.next:after{
    position: absolute;
    content: '';
    color: white;
    background-color: var(--main-color-75);
    background-image: url(/Astral/static/media/chevron-right-solid.8971e59e.svg);
    background-repeat: no-repeat;
    background-size: 1.5rem;
    background-position-y: 50%;
    right: 0;
    transition: 0.25s;
}
.next:hover:after{
    width: 8rem; 
}
@media all and (min-width:1681px){
    .row2_1{
        height: 33rem;
    }
    .row2_1>*:first-child{
        flex: 7 1;
        padding: 6rem 4rem;
    }
    .row2_1>*:last-child{
        flex: 5 1;
    }
    .next:after{
        background-position-x: 2.75rem;
